document.addEventListener('DOMContentLoaded', () => {
  // DOM   

  const onScrollHeader = () => {
    //    onScrollHeader

    const header = document.querySelector('.page-header'); //  header    

    let prevScroll = window.pageYOffset; //       
    let currentScroll; //      (  )

    window.addEventListener('scroll', () => {
      //   

      currentScroll = window.pageYOffset > 1300; //     

      const headerHidden = () => header.classList.contains('hiden-header'); //   header  

      if (currentScroll > prevScroll && !headerHidden()) {
        //      header  
        header.classList.add('hiden-header'); //   header
      }
      if (currentScroll < prevScroll && headerHidden()) {
        //      header 
        header.classList.remove('hiden-header'); //   header
      }

      prevScroll = currentScroll; //        
    });
  };

  onScrollHeader(); //    onScrollHeader
});
